"use client"

import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { useState } from "react"
import { useToast } from "@/components/ui/use-toast"

interface PaymentDialogProps {
  eventTitle: string
  amount: number
  onSuccess?: () => void
}

const banks = [
  "State Bank of India",
  "HDFC Bank",
  "ICICI Bank",
  "Axis Bank",
  "Punjab National Bank",
  "Bank of Baroda",
  "Kotak Mahindra Bank",
  "Yes Bank"
]

export function PaymentDialog({ eventTitle, amount, onSuccess }: PaymentDialogProps) {
  const [isOpen, setIsOpen] = useState(false)
  const [loading, setLoading] = useState(false)
  const [paymentMethod, setPaymentMethod] = useState("card")
  const { toast } = useToast()

  const handlePayment = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)

    try {
      // Simulate payment processing
      await new Promise(resolve => setTimeout(resolve, 1500))
      
      toast({
        title: "Payment Successful!",
        description: `You have successfully registered for ${eventTitle}`,
      })
      
      setIsOpen(false)
      onSuccess?.()
    } catch (error) {
      toast({
        title: "Payment Failed",
        description: "Please try again or contact support.",
        variant: "destructive",
      })
    } finally {
      setLoading(false)
    }
  }

  return (
    <Dialog open={isOpen} onOpenChange={setIsOpen}>
      <DialogTrigger asChild>
        <Button variant="default">Register (₹{amount})</Button>
      </DialogTrigger>
      <DialogContent className="sm:max-w-[425px]">
        <DialogHeader>
          <DialogTitle>Register for {eventTitle}</DialogTitle>
          <DialogDescription>
            Complete your payment to secure your spot
          </DialogDescription>
        </DialogHeader>

        <Tabs defaultValue="card" className="w-full" onValueChange={setPaymentMethod}>
          <TabsList className="grid w-full grid-cols-3">
            <TabsTrigger value="card">Card</TabsTrigger>
            <TabsTrigger value="upi">UPI</TabsTrigger>
            <TabsTrigger value="netbanking">NetBanking</TabsTrigger>
          </TabsList>

          <div className="mt-4">
            <h4 className="text-sm font-medium mb-2">Amount to Pay</h4>
            <p className="text-2xl font-bold mb-4">₹{amount}</p>
          </div>

          <TabsContent value="card">
            <form onSubmit={handlePayment} className="space-y-4">
              <div className="space-y-2">
                <label className="text-sm font-medium">Card Number</label>
                <Input 
                  type="text" 
                  placeholder="1234 5678 9012 3456" 
                  required 
                  pattern="[0-9\s]{16,19}"
                  maxLength={19}
                />
              </div>
              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <label className="text-sm font-medium">Expiry Date</label>
                  <Input 
                    type="text" 
                    placeholder="MM/YY" 
                    required 
                    pattern="(0[1-9]|1[0-2])\/([0-9]{2})"
                    maxLength={5}
                  />
                </div>
                <div className="space-y-2">
                  <label className="text-sm font-medium">CVV</label>
                  <Input 
                    type="password" 
                    placeholder="123" 
                    required 
                    pattern="[0-9]{3,4}"
                    maxLength={4}
                  />
                </div>
              </div>
              <Button type="submit" className="w-full" disabled={loading}>
                {loading ? "Processing..." : `Pay ₹${amount}`}
              </Button>
            </form>
          </TabsContent>

          <TabsContent value="upi">
            <form onSubmit={handlePayment} className="space-y-4">
              <div className="space-y-2">
                <label className="text-sm font-medium">UPI ID</label>
                <Input 
                  type="text" 
                  placeholder="username@upi" 
                  required 
                  pattern="[a-zA-Z0-9\.\-\_]{3,}@[a-zA-Z][a-zA-Z]{2,}"
                />
                <p className="text-sm text-gray-500">Example: username@okaxis, mobile@paytm</p>
              </div>
              <Button type="submit" className="w-full" disabled={loading}>
                {loading ? "Processing..." : `Pay ₹${amount}`}
              </Button>
            </form>
          </TabsContent>

          <TabsContent value="netbanking">
            <form onSubmit={handlePayment} className="space-y-4">
              <div className="space-y-2">
                <label className="text-sm font-medium">Select Bank</label>
                <Select required>
                  <SelectTrigger>
                    <SelectValue placeholder="Choose your bank" />
                  </SelectTrigger>
                  <SelectContent>
                    {banks.map((bank) => (
                      <SelectItem key={bank} value={bank.toLowerCase().replace(/\s+/g, '-')}>
                        {bank}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
              <Button type="submit" className="w-full" disabled={loading}>
                {loading ? "Processing..." : `Pay ₹${amount}`}
              </Button>
            </form>
          </TabsContent>
        </Tabs>
      </DialogContent>
    </Dialog>
  )
}
